import React from "react";

type Props = {
    health: number;
    armor: number;
    hunger: number;
    dehydration: number;
    temperature: number;
};

function HudsMenuData(props: Props) {
    const percent = 12000 / 100; // 1%
    const blood = props.health * percent; //  .

    return (
        <div className="huds-menu-data">
            <div>: { blood }</div>
            <div>: { props.armor }</div>
            <div>: { props.hunger }</div>
            <div>: { props.dehydration }</div>
            <div>: { props.temperature }</div>
        </div>
    );
}

export {
    HudsMenuData,
}